<template>
  <div>
    <h3 class="margin-bottom-30"></h3>
    <div class="other-tips">
      
    </div>
    <el-form
      :model="value"
      label-position="right"
      ref="doctorAndDietitianForm"
      label-width="220px"
    >
      <el-form-item
        label=""
        prop="enable"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <el-radio-group v-model="value.enable" :disabled="readOnly">
          <el-radio :label="true"></el-radio>
          <el-radio :label="false"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        label=""
        v-if="value.enable"
        prop="counts"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              :disabled="readOnly"
              size="small"
              v-model="value.counts"
              :step="1"
              :min="1"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherCounts }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="value.enable"
        label=""
        prop="counts_per_month"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              :disabled="readOnly"
              size="small"
              v-model="value.counts_per_month"
              :step="1"
              :min="1"
              :max="value.counts"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherCountsPerMonth }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        label=""
        v-if="formType === 'ROUND' && value.enable"
        prop="round_counts"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.round_counts"
              :step="1"
              :min="1"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.askHmRoundCounts }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="value.enable"
        :label="
          formType === 'ROUND'
            ? ''
            : ''
        "
        prop="first_delay_hour"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              @change="onHourChange($event, 'first')"
              :disabled="readOnly"
              size="small"
              v-model="value.first_delay_hour"
              :step="1"
              :min="0"
            ></el-input-number>
            <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-input-number
              :disabled="readOnly"
              size="small"
              v-model="value.first_delay_minute"
              :step="10"
              :max="59"
              :min="getDelayMinuteFrom().first"
            ></el-input-number>
            <span>&nbsp;</span>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherFirstDelay }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        :disabled="readOnly"
        v-if="value.enable"
        :label="
          formType === 'ROUND'
            ? ''
            : ''
        "
        prop="common_delay_hour"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              @change="onHourChange($event, 'common')"
              size="small"
              :disabled="readOnly"
              v-model="value.common_delay_hour"
              :step="1"
              :min="0"
            ></el-input-number>
            <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.common_delay_minute"
              :step="10"
              :max="59"
              :min="getDelayMinuteFrom().common"
            ></el-input-number>
            <span>&nbsp;</span>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherCommonDelay }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="value.enable"
        :label="
          formType === 'ROUND'
            ? ''
            : ''
        "
        prop="max_duration_hour"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              @change="onHourChange($event, 'duration')"
              size="small"
              :disabled="readOnly"
              v-model="value.max_duration_hour"
              :step="1"
              :min="0"
            ></el-input-number>
            <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.max_duration_minute"
              :step="10"
              :max="59"
              :min="getDelayMinuteFrom().duration"
            ></el-input-number>
            <span>&nbsp;</span>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.doctorMaxDuration }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        label=""
        prop="enable_voice"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <el-radio-group v-model="value.enable_voice" :disabled="readOnly">
          <el-radio :label="true"></el-radio>
          <el-radio :label="false"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        v-if="value.enable_voice"
        label=""
        prop="counts"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.voice_counts"
              :step="1"
              :min="1"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherVoiceCounts }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="value.enable_voice"
        label=""
        prop="counts"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.voice_counts_per_month"
              :step="1"
              :min="1"
              :max="value.voice_counts"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherVoiceCountsPerMonth }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        label=""
        v-if="value.enable"
        prop="service_days"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <el-checkbox
          :disabled="readOnly"
          :indeterminate="isIndeterminate"
          v-model="checkAllDay"
          @change="handleCheckAllChange"
          ></el-checkbox
        >
        <div style="margin: 5px 0;"></div>
        <el-checkbox-group
          :disabled="readOnly"
          v-model="value.service_days"
          @change="handleCheckedDayChange"
        >
          <el-checkbox
            v-for="day in weekdays"
            :label="day.value"
            :key="day.value"
            >{{ day.label }}</el-checkbox
          >
        </el-checkbox-group>
      </el-form-item>
      <el-form-item
        v-if="value.enable"
        label=""
        prop="serviceTime"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]"
      >
        <div style="width: 350px">
          <el-time-picker
            :readonly="readOnly"
            is-range
            format="HH : mm"
            size="small"
            v-model="value.serviceTime"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            placeholder=""
          >
          </el-time-picker>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { getWeekOptions } from '@/utils/weekDays'
import { serviceTips } from '@/utils/newService/serviceTips'

export default {
  props: {
    value: {
      type: Object,
      default: () => {}
    },
    formType: {
      type: String,
      default: ''
    },
    readOnly: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      serviceTips,
      weekdays: getWeekOptions(),
      isIndeterminate: false,
      checkAllDay: false
    }
  },
  methods: {
    onHourChange(val, type) {
      if (type === 'first') {
        if (val === 0) {
          this.value.first_delay_minute = 10
        }
      } else if (type === 'common') {
        if (val === 0) {
          this.value.common_delay_minute = 10
        }
      } else if (type === 'duration') {
        if (val === 0) {
          this.value.max_duration_minute = 10
        }
      }
    },
    getDelayMinuteFrom() {
      let firstMinuteFrom = 15
      let commonMinuteFrom = 15
      let maxDurationMinuteFrom = 15
      if (this.value.first_delay_hour > 0) {
        firstMinuteFrom = 0
      }
      if (this.value.common_delay_hour > 0) {
        commonMinuteFrom = 0
      }
      if (this.value.max_duration_hour > 0) {
        maxDurationMinuteFrom = 0
      }
      return {
        first: firstMinuteFrom,
        common: commonMinuteFrom,
        duration: maxDurationMinuteFrom
      }
    },
    handleCheckedDayChange(value) {
      let checkedCount = value.length
      this.checkAllDay = checkedCount === this.weekdays.length
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.weekdays.length
    },
    handleCheckAllChange(val) {
      this.value.service_days = val
        ? getWeekOptions().map(item => item.value)
        : []
      this.isIndeterminate = false
    },
    validForm() {
      return this.$refs.doctorAndDietitianForm.validate()
    }
  }
}
</script>

<style scoped lang="scss">
.form-item {
  display: flex;
}
.red-tips {
  font-size: 12px;
  color: #f8747f;
}
.other-tips {
  margin-top: -10px;
  margin-left: 20px;
  font-size: 12px;
  color: #f8747f;
}
</style>
